import React, {useState} from 'react'
import {Link} from 'react-router-dom'


function Navbar() {
    return (
        //fragments
         //link = a tag
        <>
            <nav className ='navbar'>
                <div className='navbar-container'>
                    <Link to="/" className="navbar-logo">Home</Link>
                </div>
            </nav>
        </>
    )
}

export default Navbar
